// src/app/dashboard/page.tsx

import DashboardHeader from '@/components/dashboard/DashboardHeader';
// İstatistik kartları için ikonlar
import { Users, Calendar, Clock, BarChart2, UserCircle } from 'lucide-react';
import Link from 'next/link';

// FAZ 2 (Mock Data): Bu veriler yakında API'den (NestJS) gelecek
const stats = {
  toplamHasta: 247,
  bugunkuRandevu: 5,
  yaklasanRandevu: 12,
  onayBekleyen: 3,
};

const mockUpcomingAppointments = [
  { id: 1, name: 'Ayşe Yılmaz', time: '10:00' },
  { id: 2, name: 'Mehmet Can', time: '11:00' },
  { id: 3, name: 'Taha Yüce', time: '14:00' },
];

export default function DashboardOverviewPage() {
  return (
    <div className="p-8">
      {/* 1. Üst Başlık (Yeni Bileşen) */}
      <DashboardHeader title="Genel Bakış" />

      {/* 2. İstatistik Kartları */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        
        {/* Kart 1: Bugünkü Randevu */}
        <StatCard 
          title="Bugünkü Randevu" 
          value={stats.bugunkuRandevu.toString()} 
          icon={Clock} 
          color="text-blue-500"
        />
        {/* Kart 2: Onay Bekleyen */}
        <StatCard 
          title="Onay Bekleyen" 
          value={stats.onayBekleyen.toString()} 
          icon={Calendar} 
          color="text-yellow-500"
        />
        {/* Kart 3: Toplam Hasta */}
        <StatCard 
          title="Toplam Hasta" 
          value={stats.toplamHasta.toString()} 
          icon={Users} 
          color="text-green-500"
        />
        {/* Kart 4: Aylık Ortalama */}
        <StatCard 
          title="Aylık Ortalama" 
          value={"45"} // Örnek veri
          icon={BarChart2} 
          color="text-indigo-500"
        />
      </div>

      {/* 3. Son Aktiviteler / Hızlı Bakış */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        
        {/* Sol Kısım: Yaklaşan Randevular (Bugün) */}
        <div className="lg:col-span-2 bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold text-gray-800 mb-4">Bugünkü Yaklaşan Randevular</h3>
          <div className="space-y-4">
            {mockUpcomingAppointments.map(app => (
              <div key={app.id} className="flex justify-between items-center p-3 hover:bg-gray-50 rounded-lg">
                <div className="flex items-center space-x-3">
                  <div className="p-2 bg-indigo-100 rounded-full">
                    <UserCircle className="w-5 h-5 text-indigo-600" />
                  </div>
                  <div>
                    <p className="font-medium text-gray-700">{app.name}</p>
                    <p className="text-sm text-gray-500">{app.time}</p>
                  </div>
                </div>
                <Link href="/dashboard/randevular" className="text-sm font-medium text-indigo-600 hover:underline">
                  Detay
                </Link>
              </div>
            ))}
            {mockUpcomingAppointments.length === 0 && (
              <p className="text-gray-500">Bugün için planlanmış randevu bulunmamaktadır.</p>
            )}
          </div>
        </div>

        {/* Sağ Kısım: Hızlı Eylemler (Örnek) */}
        <div className="bg-white p-6 rounded-lg shadow-md">
           <h3 className="text-xl font-semibold text-gray-800 mb-4">Hızlı Eylemler</h3>
           <div className="space-y-3">
             <Link href="/dashboard/randevular" className="block w-full text-center p-3 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700">
               Tüm Randevuları Görüntüle
             </Link>
             <Link href="/dashboard/hastalar" className="block w-full text-center p-3 bg-gray-200 text-gray-800 rounded-lg hover:bg-gray-300">
               Yeni Hasta Ekle
             </Link>
           </div>
        </div>
      </div>

    </div>
  );
}

// İstatistik Kartı için yardımcı bileşen
function StatCard({ title, value, icon: Icon, color }: { title: string, value: string, icon: React.ElementType, color: string }) {
  return (
    <div className="bg-white p-6 rounded-lg shadow-md flex items-center space-x-4">
      <div className={`p-3 rounded-full bg-opacity-10 ${color.replace('text-', 'bg-')}`}>
        <Icon className={`w-6 h-6 ${color}`} />
      </div>
      <div>
        <p className="text-sm font-medium text-gray-500">{title}</p>
        <p className="text-3xl font-bold text-gray-900">{value}</p>
      </div>
    </div>
  );
}